import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { Box, Typography, Button, TextField, Grid, Paper, Container, CircularProgress, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import axios from 'axios';
import cinemaBanner from '../../assets/cinema-banner.png';
import cinemaFooter from '../../assets/cinema-footer.png';
import { Bar } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';
import 'chartjs-plugin-datalabels';

Chart.register(...registerables);

const CinePulseFaceDetectorForm = () => {
  const { state } = useLocation();
  const movie = state?.movie;
  const navigate = useNavigate();
  const [files, setFiles] = useState(Array(5).fill(null));
  const [loading, setLoading] = useState(false);
  const [chartData, setChartData] = useState(null);
  const apiBaseUrl = process.env.REACT_APP_API_BASE_URL;

  const handleBannerClick = () => {
    navigate('/cinepulse-face-detector');
  };

  const handleFileChange = (event, index) => {
    const newFiles = [...files];
    newFiles[index] = event.target.files[0];
    setFiles(newFiles);
  };

  const handleSubmit = async () => {
    setLoading(true);
    const formData = new FormData();
    files.forEach((file, index) => {
      if (file) formData.append(`file${index}`, file);
    });

    try {
      const response = await axios.post(`${apiBaseUrl}` +'/api/getMovieReviewPredictions', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      setLoading(false);
      setChartData(prepareChartData(response.data.total_emotions));
    } catch (error) {
      console.error('Error uploading files:', error);
      setLoading(false);
    }
  };

  const prepareChartData = (data) => {
    return {
      labels: Object.keys(data),
      datasets: [{
        label: 'Emotions',
        data: Object.values(data),
        backgroundColor: 'rgba(255, 99, 132, 0.8)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 1
      }]
    };
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        labels: {
          color: 'white'
        }
      },
      tooltip: {
        bodyColor: 'white',
        titleColor: 'white'
      }
    },
    scales: {
      y: {
        beginAtZero: true,
        ticks: {
          color: 'white'
        }
      },
      x: {
        ticks: {
          color: 'white'
        }
      }
    }
  };

  const renderEmotionSummary = () => {
    if (!chartData) return null;
    const total = chartData.datasets[0].data.reduce((a, b) => a + b, 0);
    return (
      <TableContainer component={Paper} sx={{ maxWidth: 400, backgroundColor: '#222', mt: 2 }}>
        <Table size="small" aria-label="emotion summary">
          <TableHead>
            <TableRow>
              <TableCell style={{ color: 'white' }}>Emotion</TableCell>
              <TableCell style={{ color: 'white' }} align="right">Percentage</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {chartData.labels.map((emotion, index) => (
              <TableRow key={emotion}>
                <TableCell component="th" scope="row" style={{ color: 'white' }}>
                  {emotion}
                </TableCell>
                <TableCell style={{ color: 'white' }} align="right">
                  {((chartData.datasets[0].data[index] / total) * 100).toFixed(2)}%
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    );
  };

  if (!movie) {
    return <Typography variant="h6" color="error" sx={{ p: 4 }}>Movie data not available.</Typography>;
  }

  return (
    <Container maxWidth={false} sx={{ minHeight: '100vh', backgroundColor: "#000", color: "#fff", padding: 0 }}>
      <img 
        src={cinemaBanner} 
        alt="Cinema Banner" 
        style={{ width: '100%', height: '400px', display: 'block', cursor: 'pointer' }} 
        onClick={handleBannerClick}
      />
      <Grid container spacing={4} justifyContent="center" sx={{ p: 4 }}>
        <Grid item xs={12} md={6}>
          <Paper elevation={12} sx={{ padding: 3, backgroundColor: "#222", color: "#fff", display: 'flex', flexDirection: 'column', alignItems: 'center', borderRadius: '12px' }}>
            <img src={movie.image} alt={movie.title} style={{ width: '100%', maxHeight: '400px', objectFit: 'contain', borderRadius: '8px' }} />
            <Typography variant="h4" sx={{ mt: 2 }}>{movie.title}</Typography>
            <Typography variant="body1" textAlign="center" sx={{ mt: 1 }}>{movie.description}</Typography>
            <Typography variant="h6" sx={{ mt: 1 }}>Release Year: {movie.releaseYear}</Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} md={6}>
          <Paper elevation={12} sx={{ padding: 3, backgroundColor: "#222", color: "#fff", borderRadius: '12px', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <Typography variant="h6">Upload for Movie Analysis</Typography>
            {!chartData ? 
              <>
                {[...Array(5)].map((_, index) => (
                  <TextField
                    key={index}
                    type="file"
                    fullWidth
                    variant="outlined"
                    InputProps={{
                      style: {
                        color: '#fff',
                        borderRadius: '20px', 
                        height: '56px' 
                      }
                    }}
                    onChange={(event) => handleFileChange(event, index)}
                    sx={{ mt: 2, '.MuiOutlinedInput-root': { borderRadius: '20px' } }}
                  />
                ))}
                <Button 
                  variant="contained" 
                  color="primary" 
                  sx={{ mt: 13, borderRadius: '20px', width: '50%' }}
                  onClick={handleSubmit}
                >
                  Submit Videos
                </Button>
                {loading && <CircularProgress size={24} sx={{ position: 'absolute', mt: 4 }} />}
              </>
            : 
              <>
                <Bar data={chartData} options={options} />
                {renderEmotionSummary()}
              </>
            }
          </Paper>
        </Grid>
      </Grid>
      <Box sx={{ width: '100%', overflow: 'hidden', mt: 4 }}>
        <img src={cinemaFooter} alt="Cinema Footer" style={{ width: '100%', display: 'block' }} />
      </Box>
    </Container>
  );
};

export default CinePulseFaceDetectorForm;
