import {
  BrowserRouter,
  Routes,
  Route
} from "react-router-dom";
import "./stylesheets/app.scss";
import CinePulseFaceDetector from "./pages/cinepulse-face-detector/CinePulseFaceDetector";
import CinePulseFaceDetectorForm from "./components/cinepulse-face-detector-form/CinePulseFaceDetectorForm";
import CinePulseLiveMovieReview from "./components/cinepulse-live-movie-review/CinePulseLiveMovieReview";

function App() {
  return (
    <BrowserRouter>
        <Routes>
          <Route path="/" element={<CinePulseFaceDetector />} />
          <Route path="/cinepulse-face-detector" element={<CinePulseFaceDetector />} />
          <Route path="/cinepulse-face-detector/movie-details/:title" element={<CinePulseFaceDetectorForm />} />
          <Route path="/cinepulse-face-detector/live-movie-review" element={<CinePulseLiveMovieReview />} />
      </Routes>
    </BrowserRouter>
  );
}
export default App;
