import React from 'react';
import { Box, Container, Grid, Typography, Card, CardMedia, CardContent, CardActionArea } from "@mui/material";
import { useNavigate } from 'react-router-dom';

import cinemaBanner from "../../assets/cinema-banner.png";
import cinemaFooter from "../../assets/cinema-footer.png";
import inceptionPoster from '../../assets/inception_poster.jpg';
import interstellarPoster from '../../assets/interstellar_poster.webp';
import matrixPoster from '../../assets/matrix_poster.jpg';
import bladeRunnerPoster from '../../assets/blade_runner_poster.jpg';
import avatarPoster from '../../assets/avatar_poster.jpg';
import gravityPoster from '../../assets/gravity_poster.webp';
import arrivalPoster from '../../assets/arrival_poster.jpg';
import dunePoster from '../../assets/dune_poster.webp';
import martianPoster from '../../assets/martian_poster.jpg';
import prestigePoster from '../../assets/prestige_poster.jpg';
import liveReviewImage from '../../assets/live_review_image.jpg'; // Assume you have an image for live review

const MovieCard = ({ movie }) => {
  const navigate = useNavigate();

  const handleCardClick = () => {
    navigate(`/cinepulse-face-detector/movie-details/${encodeURIComponent(movie.title)}`, { state: { movie } });
  };

  return (
    <Card sx={{ maxWidth: 345, backgroundColor: "#333", color: "#fff" }}>
      <CardActionArea onClick={handleCardClick}>
        <CardMedia
          component="img"
          image={movie.image}
          alt={movie.title}
          sx={{ height: 500 }}
        />
        <CardContent sx={{ textAlign: 'center' }}>
          <Typography gutterBottom variant="h5" component="div">
            {movie.title}
          </Typography>
          <Typography variant="body1">
            {movie.description}
          </Typography>
          <Typography variant="h6" display="block" gutterBottom>
            Release Year: {movie.releaseYear}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};

