import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Box, Button, Container, Grid, Typography, CircularProgress } from '@mui/material';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import cinemaBanner from '../../assets/cinema-banner.png';
import cinemaFooter from '../../assets/cinema-footer.png';
import useWebSocket from '../../hooks/useWebSocket';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const CinePulseLiveMovieReview = () => {
  const [isRecording, setIsRecording] = useState(false);
  const [chartData, setChartData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [snapshotUrl, setSnapshotUrl] = useState(null);
  const [emotionData, setEmotionData] = useState(null);
  const wsData = useWebSocket('ws://localhost:3001/ws/emotion/');
  const apiBaseUrl = process.env.REACT_APP_API_BASE_URL;

  useEffect(() => {
    if (wsData && isRecording) {
      setEmotionData(wsData);
    }
  }, [wsData, isRecording]);

  useEffect(() => {
    let intervalId;
    if (isRecording) {
      const fetchImage = () => {
        axios.get(`${apiBaseUrl}` + '/api/getCameraSnapshot/', { responseType: 'blob' })
          .then(response => {
            const imageUrl = URL.createObjectURL(response.data);
            setSnapshotUrl(imageUrl);
          })
          .catch(error => console.error('Error fetching image:', error));
      };
      fetchImage();  // Call immediately and then set interval
      intervalId = setInterval(fetchImage, 1000); // Fetch every second only if recording
    } else if (intervalId) {
      clearInterval(intervalId);  // Clear the interval when not recording
    }
    return () => {
      if (intervalId) {
        clearInterval(intervalId); // Clear the interval when the component unmounts
      }
    };
  }, [isRecording]);
  

  const handleStartRecording = async () => {
    setIsRecording(true);
    setLoading(true);
    await axios.get(`${apiBaseUrl}` + '/api/startRecording/')
      .then(() => setLoading(false))
      .catch(error => {
        console.error('Error starting recording:', error);
        setLoading(false);
      });
  };

  const handleStopRecording = async () => {
    setIsRecording(false);  // Set this immediately to stop further actions
    setLoading(true);
    try {
      const response = await axios.get(`${apiBaseUrl}` + '/api/stopRecording/');
      setChartData(prepareChartData(response.data.emotion_counts));
      setEmotionData(null); // Optionally clear emotion data
    } catch (error) {
      console.error('Error stopping recording:', error);
    }
    setLoading(false);
    setSnapshotUrl(null);  // Clear the snapshot URL on stop recording

  };
  


  const prepareChartData = (data) => {
    return {
      labels: Object.keys(data),
      datasets: [{
        label: 'Emotion Count',
        data: Object.values(data),
        backgroundColor: 'rgba(255, 99, 132, 0.8)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 1
      }]
    };
  };

  return (
    <Container maxWidth={false} sx={{ minHeight: '100vh', backgroundColor: "#000", color: "#fff", padding: 0 }}>
      <Grid container spacing={2} justifyContent="center">
        <Grid item xs={12}>
          <Box sx={{ width: '100%', height: '50vh', overflow: 'hidden' }}>
            <img src={cinemaBanner} alt="Cinema Banner" style={{ height: '400px', width: '100%' }} />
          </Box>
        </Grid>
        <Grid item xs={6}>
        <Box sx={{ width: '100%', height: 400, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
  {isRecording ? (
    <CircularProgress /> // Display CircularProgress while recording
  ) : (
    // Display the snapshot image if available
    snapshotUrl && <img src={snapshotUrl} alt="Live Feed" style={{ width: '100%', marginTop: '20px' }} />
  )}
</Box>

      </Grid>
        <Grid item xs={6} textAlign="center">
          <Typography variant="h4" gutterBottom>Live Movie Review</Typography>
          {loading}
          {!isRecording && <Button variant="contained" sx={{ borderRadius: '20px', width: '50%' }} onClick={handleStartRecording}>Start Live Review</Button>}
          {isRecording && <Button variant="contained" sx={{ borderRadius: '20px', width: '50%' }} color="error" onClick={handleStopRecording}>Stop Recording</Button>}
          {emotionData && (
            <Typography variant="h6" sx={{ mt: 2, color: 'white' }}>
              Current Detection - Person {emotionData.personId}: {emotionData.emotion} at {emotionData.index.toFixed(2)}%
            </Typography>
        )}
          {!isRecording && chartData && (
            <Box sx={{ mt: 2 }}>
              <Bar data={chartData} options={{
                scales: {
                  y: {
                    beginAtZero: true,
                    ticks: { color: '#fff' }
                  },
                  x: {
                    ticks: { color: '#fff' }
                  }
                },
                plugins: {
                  legend: {
                    labels: { color: '#fff' }
                  }
                }
              }} />
            </Box>
          )}
        </Grid>
        <Grid item xs={12}>
          <Box sx={{ width: '100%', overflow: 'hidden' }}>
            <img src={cinemaFooter} alt="Cinema Footer" style={{ width: '100%' }} />
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
};

export default CinePulseLiveMovieReview;
